import React from "react";
import { Box, Typography, Grid, Card } from "@mui/material";
import Carousel from "react-material-ui-carousel";
import zombieChef from "../assets/CryptoChefs/zombie.png";
import hipsterChef from "../assets/CryptoChefs/hipster.png";
import ninjaChef from "../assets/CryptoChefs/ninja.png";
import back from "../assets/Arrows/back-arrow.svg";
import forward from "../assets/Arrows/forward-arrow.svg";

const MeetCryptoChefsComponent = () => {
  const chefs = [
    {
      name: "Zombie Chef",
      image: zombieChef,
    },
    {
      name: "Hipster Chef",
      image: hipsterChef,
    },
    {
      name: "Ninja Chef",
      image: ninjaChef,
    },
  ];

  return (
    <Box sx={{ backgroundColor: "secondary.main" }}>
      <Box
        sx={{
          width: "100%",
          height: "9vw",
          marginBottom: "2vw",
          backgroundColor: "primary.main",
          clipPath: "ellipse(67% 220% at 50% -120%)",
        }}
      ></Box>
      <Grid container flexDirection="column" alignItems="center">
        <Grid item>
          <Typography item variant="h2">
            MEET THE CRYPTOCHEFS!
          </Typography>
        </Grid>
        <Grid container item justifyContent="center" mt="3vw">
          <Grid item md={7}>
            <Carousel
              interval="7000"
              navButtonsAlwaysVisible
              indicatorIconButtonProps={{
                style: {
                  color: "transparent",
                },
              }}
              navButtonsProps={{
                style: {
                  backgroundColor: "transparent",
                  marginTop: "-2vw",
                  width: "7vw",
                  height: "7vw",
                },
              }}
              PrevIcon={<img src={back} width="50%" alt="PrevIcon"></img>}
              NextIcon={<img src={forward} width="50%" alt="NextIcon"></img>}
            >
              {chefs.map((chef, index, array) => (
                <Box sx={{ display: "flex", justifyContent: "center" }}>
                  {index === 0 ? (
                    <Card style={{ marginRight: "-23%" }}>
                      <img
                        src={array[array.length - 1].image}
                        alt={array[array.length - 1].name}
                        width="100%"
                      ></img>
                    </Card>
                  ) : (
                    <Card style={{ marginRight: "-23%" }}>
                      <img
                        src={array[index - 1].image}
                        alt={array[index - 1].name}
                      ></img>
                    </Card>
                  )}
                  <Box width="28vw" height="36vw">
                    <img src={chef.image} width="100%" alt={chef.name}></img>
                    <Typography variant="h3" textAlign="center" mt="-3.2vw">
                      {chef.name}
                    </Typography>
                  </Box>
                  {index === array.length - 1 ? (
                    <Card style={{ marginLeft: "-23%" }}>
                      <img src={array[0].image} alt={array[0].name}></img>
                    </Card>
                  ) : (
                    <Card style={{ marginLeft: "-23%" }}>
                      <img
                        src={array[index + 1].image}
                        alt={array[index + 1].name}
                      ></img>
                    </Card>
                  )}
                </Box>
              ))}
            </Carousel>
          </Grid>
        </Grid>
      </Grid>
      <Box
        sx={{
          width: "100%",
          height: "9vw",
          marginTop: "12vw",
          backgroundColor: "primary.main",
          clipPath: "ellipse(67% 220% at 50% 220%)",
        }}
      ></Box>
    </Box>
  );
};

export default MeetCryptoChefsComponent;
