import React from "react";
import { Typography, Grid, ButtonUnstyled } from "@mui/material";
import mainImage from "../assets/hero.png";
import playBubble from "../assets/play-bubble.svg";
import { styled } from "@mui/system";

const CustomButtonRoot = styled("button")`
  background: transparent url(${playBubble}) no-repeat center;
  background-size: contain;
  padding: 30px;
  padding-top: 35px;
  border: none;
  cursor: pointer;
  &:hover {
    transform: translateY(2px);
  }
`;

function CustomButton(props) {
  return <ButtonUnstyled {...props} component={CustomButtonRoot} />;
}

const LandingComponent = () => {
  return (
    <Grid
      container
      flexDirection="column"
      alignItems="center"
    >
      <Grid item>
        <Typography variant="h1">COOK. STAKE. EARN.</Typography>
      </Grid>
      <Grid container item justifyContent="center" alignItems="center">
        <Grid container item justifyContent="center">
          <Grid item>
            <img src={mainImage} width="100%" alt="Play"></img>
          </Grid>
        </Grid>
        <Grid item sx={{ position: "relative", bottom: "360px" }}>
          <CustomButton>
            <Typography
              variant="h2"
              sx={{
                color: "secondary.main",
                marginTop: "0",
                fontSize: "45px",
              }}
            >
              PLAY
            </Typography>
          </CustomButton>
        </Grid>
      </Grid>
    </Grid>
  );
};
export default LandingComponent;
